@mixin on-hover-shadow {
    box-shadow: 0 1px 3px 1px gray;
    transition: 0.25s box-shadow;
    &:hover {
        box-shadow: 0 4px 8px 2px gray;
        cursor: pointer;
    }
}
@mixin centered-position {
    display: flex;
    justify-content: center;
    align-items: center;
}
@mixin centered-position-column {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
@mixin keyframes($animation-name) {
     @keyframes #{$animation-name}{
        @content;
    }
}
@mixin animation($str) {
    animation: #{$str};
}
